<template>
  <div>
    <div class="cell" v-on:click="y">
      <template v-if="a">{{text}}</template>
      <template v-else></template>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      a: false,
      text: ""
    };
  },
  props: ["n"],
  methods: {
    y() {
      if (this.text !== "") {
        return;
      }
      this.a = true;
      this.text = this.n % 2 === 0 ? "x" : "o";
      this.$emit("click", this.text);
    }
  }
};
</script>
 
<style>
.cell {
  border: 1px solid black;
  width: 100px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 80px;
}
</style>